import React from 'react';
import PropTypes from 'prop-types';
import List from 'material-ui/List'

import TagSingle from '../tag-single';
import TagListArray from '../tag-list-array';

const propTypes = {
  /** query tag name, also used for TagSingle/TagArray switch */
  qTag: PropTypes.string,
  /** True enables onClick for Chip */
  navClick: PropTypes.bool,
  /** ['cookie', 'milk'] or 'cookie,milk' */
  tagList: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.array,
  ]),
};

const defaultPorps = {
  qTag: '',
  navClick: false,
  tagList: '',
};

const TagList = ({ qTag, navClick, tagList }) => {
  if (qTag) {
    return (
      <List>
        <TagSingle tag={qTag} />
      </List>
    );
  }
  return (
    <List>
      <TagListArray
        tags={tagList}
        navClick={navClick}
      />
    </List>
  );
};

TagList.propTypes = propTypes;
TagList.defaultProps = defaultPorps;

export default TagList;



