import React from 'react';
import { withStyles } from 'material-ui/styles';
import Fade from 'material-ui/transitions/Fade';

import ResponsiveDrawer from '../components/responsive-drawer';
import GridBlock from '../components/grid-block';
import GridColumn from '../components/grid-column';
import GridItem from '../components/grid-item';
import PostExcerpt from '../components/post-excerpt';
import TagBar from '../components/tag-bar';
import lib from '../lib';

const styles = theme => ({
  root: {
    // color: 'green',
    fontFamily: theme.typography.fontFamily,
    // fontSize: theme.typography.fontSize,
    // fontWeight: theme.typography.fontWeight,
  },
});

class Index extends React.Component {

  getPostList() {
    const postList = [];

    const qTag = this.props.location.search.slice(3);
    this.props.data.allMarkdownRemark.edges.forEach((edge) => {

      if (edge.node.frontmatter.tags.indexOf(qTag) !== -1) {
        postList.push({
          excerpt: edge.node.excerpt,
          html: edge.node.html.substr(0, edge.node.html.indexOf('<hr>')),
          slug: edge.node.fields.slug,
          path: edge.node.frontmatter.path,
          title: edge.node.frontmatter.title,
          date: edge.node.frontmatter.date,
          tags: edge.node.frontmatter.tags,
          cover: edge.node.frontmatter.cover
            ? edge.node.frontmatter.cover.childImageSharp.responsiveSizes
            : null,
        });
      }
    });
    return postList;
  }

  getTagList() {
    const tagList = [];
    this.props.data.allMarkdownRemark.edges.forEach((edge) => {
      const tmpArr = lib.csvStringToArray(edge.node.frontmatter.tags);
      tagList.push(...tmpArr);
    });
    return Array.from(new Set(tagList));
  }

  render() {
    const qTag = this.props.location.search.slice(3);
    const tagList = this.getTagList();
    const postList = this.getPostList();
    const { classes } = this.props;
    return (
      <Fade in={true} timeout={600}>
      <div className={classes.root}>
        <ResponsiveDrawer tagList={tagList} qTag={qTag} navClick />

          <GridBlock>

            <GridColumn>
              {postList.map(page => (
                <GridItem key={page.path} page={page} PostType={PostExcerpt} />
              ))}
            </GridColumn>

          </GridBlock>

      </div>
      </Fade>
    );
  }
}

/* eslint no-undef: "off"*/
export const pageQuery = graphql`
  query IndexQuery {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      limit: 2000,
      sort: { fields: [frontmatter___date], order: DESC },
    ) {
      edges {
        node {
          excerpt(pruneLength: 250)
          fields {
            slug
          }
          html
          frontmatter {
            title
            date
            path
            parent
            tags
            description
            cover {
              childImageSharp {
                responsiveSizes(maxWidth: 1200) {
                  src
                  sizes
                  srcSet
                  originalImg
                  originalName
                  aspectRatio
                  base64
                }
              }
            }
          }
        }
      }
    }
  }
`;

export default withStyles (styles, { name: 'styledIndex' })(Index);



